.ext-view th {
  background-color: #808080;
  color: #fff;
  padding: 0 5px;
}

.ext-view td {
  width: 125px !important;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-top: 0px solid black;
  border-bottom: 0px solid black;
  border-collapse: collapse;
  background-color: #f3f3f35d;
}

.ext-view {
  /* margin-bottom: 50px; */
  display: flex;
  width: 100%;
  height: 290px;
  justify-content: space-around;
  transform: scale(0.9);
}

.ext-view table {
  width: 100%;
  overflow: auto;
}

td.border-strong-top {
